.FixedLayout.PanelHeaderContext {
    z-index: 4!important;
}

.vkuiTabsItem__label {
    text-align: center;
}

.ScheduleHeader {
    justify-content: center;
    text-align: center;
}

.ScheduleCardInner {
    padding: 10px;
    display: flex;
    column-gap: 10px;
}

.ScheduleCardInner__Separator {
    width: 5px;
    border-radius: 10px;
    background-color: gray;
}

.ScheduleCardInner__Separator.red {
    background-color: var(--vkui--color_background_negative);
}

.ScheduleCardInner__Separator.green {
    background-color: var(--vkui--color_background_positive);
}

.ScheduleCardInner__Separator.yellow {
    background-color: var(--vkui--color_accent_orange);
}

.ScheduleCardInner__Time {
    text-align: center;
}